function sliderMenuBlocks() {
    const swiper = new Swiper(".menu-blocks .mySwiper", {
        slidesPerView: "auto",
        spaceBetween: 18,
        slideToClickedSlide: true,
        navigation: {
            nextEl: ".menu-blocks .swiper-button-next",
            prevEl: ".menu-blocks .swiper-button-prev"
        },
        on: {
            // После инициализации или изменения слайдов
            init: checkOverflow,
            slideChange: checkOverflow,
        }
    });

    // Функция для проверки, есть ли переполнение
    function checkOverflow() {
        const container = document.querySelector('.menu-blocks');
        const wrapper = document.querySelector('.menu-blocks .mySwiper .swiper-wrapper');

        // Общая ширина всех слайдов
        const slidesWidth = Array.from(wrapper.children).reduce((total, slide) => {
            return total + slide.offsetWidth + parseInt(getComputedStyle(slide).marginRight);
        }, 0);

        // Ширина контейнера
        const containerWidth = container.offsetWidth;

        // Если ширина слайдов больше ширины контейнера, добавляем класс
        if (slidesWidth > containerWidth) {
            container.classList.add('overflow');
        } else {
            container.classList.remove('overflow');
        }
    }

    // Вызовем проверку сразу после инициализации
    checkOverflow();
}

